<template>
	<div class="swiper-container" :class="swiperid">
		<div class="swiper-wrapper"><!--  -->
			<slot name="swiper-con"></slot>	
		</div>
		<div :class="{'swiper-pagination': pagination}" :style="{'text-align': paginationDiraction}"><!--   -->
			
		</div>
	</div>
</template>
<script type="text/javascript">
	import "@/assets/lips/js/swiper.js"/*js*/
	
	export default{
		props: {
			swiperid: {//
				type: String,
				default: ''
			},
			effect: {//
				type: String,
				default: 'slide'
			},
			loop: {//
				type: Boolean,
				default: true
			},
			diraction: {//
				type: String,
				default: 'horizantal'
			},
			autoplay: {//
				type: Number,
				default: 2000
			},
			paginationType: {//
				type: String,
				default: 'bullets'
			},
			pagination: {//
				type: Boolean,
				default: true
			},
			paginationDiraction: {//
				type: String,
				default: 'center'
			}
		},
		mounted: function(){//
			var _this = this;//thisvm
			new Swiper("."+_this.swiperid, {//swiper 
				loop: _this.loop,
				pagination: ".swiper-pagination",
				paginationType: _this.paginationType,
				autoplay: parseInt(_this.autoplay),
				diraction: _this.diraction,
				effect: _this.effect,
				autoplayDisableOnInteraction: false
			})
		}
	}
</script>
<style type="text/css">
	@import "../../assets/lips/css/swiper.min.css";
	.swiper-container img{width:375px;}
	.swiper-container{border-bottom:1px solid #8b898b;}
</style>


