<template>
	<div class="kindWrap">
		<div class="inner" v-for="(item,index) in arr">
			<h3>{{item.tabs}}</h3>
			<div class="type" v-for="(subitem,i) in item.subarr">
				<div class="t_pic">
					<img :src="subitem.picsrc" />
				</div>
				<div class="t_con">
					<h2>{{subitem.title}}</h2>
					<p>{{subitem.text}}</p>
				</div>
				<span><em>{{subitem.num}}</em></span>
				<a href="javascript:;" @click="subitem.bool=!subitem.bool">
					<img src="../../assets/images/ic_group_check_anonymous.png" v-show="subitem.bool">
					<img src="../../assets/images/ic_group_checked_anonymous.png" v-show="!subitem.bool">
				</a>
			</div>
		</div>
