</script>
<style type="text/css">
	.kindWrap{width:100%;background-color: #fff;}
	.kindWrap .inner{border-top:1px solid #eaeaea;width:345px;margin:0 auto;position:relative;padding:12px 0;}
	.kindWrap .inner h3{font-size:16px;width:71px;height:25px;position:absolute;top:-12px;left:137px;line-height:25px;background-color:#fff;}
	.kindWrap .type{margin:15px 0;position:relative;}
	.kindWrap .t_pic{float:left;}
	.kindWrap .t_pic img{width:50px;}
	.kindWrap .t_con{float:left;width:185px;margin-left:10px;text-align:left;}
	.kindWrap .t_con h2{height:22px;overflow:hidden;font-size:16px;}
	.kindWrap .t_con p{height:28px;overflow:hidden;line-height:14px;font-size:12px;color:#ccc;}
	.kindWrap .inner span{font-size:12px;color:#ccc;height:50px;line-height:50px;display:inline-block;margin-left:-30px;}
	.kindWrap .inner a{display:inline-block;height:50px;line-height:50px;position:absolute;top:5px;right:0;}
</style>


