<template>
	<div class="audio">
		<Myheader con="">
			<img src="../assets/images/ic_chat_green.png" slot="one" class="audio_one">
			<img src="../assets/images/ic_group_search.png" slot="two" class="audio_two">
		</Myheader>
		<ul>
			<li :class="{active: nowIndex==1}" @click="nowIndex=1">
				<router-link to="/audio/film"></router-link>
			</li>
			<li :class="{active: nowIndex==2}" @click="nowIndex=2">
				<router-link to="/audio/read"></router-link>
			</li>
			<li :class="{active: nowIndex==3}" @click="nowIndex=3">
				<router-link to="/audio/mv"></router-link>
			</li>
			<li :class="{active: nowIndex==4}" @click="nowIndex=4">
				<router-link to="/audio/city"></router-link>
			</li>
			<li :class="{active: nowIndex==5}" @click="nowIndex=5">
				<router-link to="/audio/music"></router-link>
			</li>
		</ul>
		<div class="content">
			<router-view></router-view>
		</div>
	</div>
</template>
<script type="text/javascript">
	//import Subnav from "../components/subnav"
	import Myheader from "../components/header"
	export default{
		components: {
			Myheader
		},
		data: function(){
			return {
				nowIndex: 1
			}
		}
	}
</script>
<style type="text/css">
	.audio{position:relative;background-color:#f7f7f7;}
	.audio img{width:25px;}
	.audio .audio_one{position: absolute;top:10px;right:15px;}
	.audio .audio_two{position: absolute;top:10px;right:70px;}
	.audio ul{width:100%;height:39px;border-bottom:1px solid #ececec;background-color:#fff;}
	.audio ul li{height:37px;width:20%;float:left;border-bottom:2px solid #fff;line-height:37px;list-style:none;}
	.audio ul li a{color:#666;text-decoration:none;font-size:14px;}
	.audio ul .active{border-bottom-color:#64ca80;}
	.audio ul .active a{color:#64ca80;}
</style> 


