<template>
	<div class="tabWrap">
		<slot name="pic"></slot><br/>
		<span>{{text}}</span>
	</div>
</template>
<script type="text/javascript">
	export default{
		props: ["text"]
	}
</script>
<style type="text/css">
	.tabWrap{height:60px;width:25%;float:left;}
	.tabWrap span{height:30px;line-height:30px;font-size:12px;color:#666;display:inline-block;}
</style>


