<template>
	<div class="bcast">
		<Myheader con="">
			<img src="../assets/images/ic_chat_green.png" slot="one" class="bcast_one">
			<img src="../assets/images/ic_status_search_user.png" slot="two" class="bcast_two">
		</Myheader>
		<div class="find">
			<img src="../assets/images/ic_status_anonymous_header.png">
			<h3></h3>
			<p></p>
		</div>
		<Consumer></Consumer>
		<Shai_xuan></Shai_xuan>
	</div>
</template>
<script type="text/javascript">
	import Myheader from "../components/header"
	import Consumer from "../components/broadcast/consumer"
	import Shai_xuan from "../components/broadcast/ShaiXuan"
	export default{
		components: {
			Myheader,
			Consumer,
			Shai_xuan
		}
	}
</script>
<style type="text/css">
	.bcast{position:relative;background-color:#f7f7f7;}
	.bcast img{width:25px;}
	.bcast .bcast_one{position: absolute;top:10px;right:15px;}
	.bcast .bcast_two{position: absolute;top:10px;right:70px;}
	.bcast .find{text-align:left;}
	.bcast .find img{width:90px;float:left;margin:-17px 20px 0 50px;}
	.bcast .find h3{height:24px;line-height:24px;margin-top:37px;font-size:16px;}
	.bcast .find p{height:20px;line-height:20px;font-size:12px;color:#666;padding-bottom:34px;}
</style>


