<template>
	<div class="main">
		<Myheader con="">
			<img src="../assets/images/ic_settings.png" slot="one" class="main_one">
		</Myheader>
		<Onload></Onload>
		<Remind></Remind>
		<Partab></Partab>
	</div>
</template>
<script type="text/javascript">
	import Myheader from "../components/header"
	import Onload from "../components/main/Onload"
	import Remind from "../components/main/remind"
	import Partab from "../components/main/partab"
	export default{
		components: {
			Myheader,
			Onload,
			Remind,
			Partab
		}
	}
</script>
<style type="text/css">
	.main{position:relative;background-color:#eee;height:667px;}
	.main img{width:25px;}
	.main .main_one{position: absolute;top:10px;right:15px;}
	.main .main_two{position: absolute;top:10px;right:70px;}
</style>


