import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Home from '../page/home'
import Audio from '../page/audio'
import Broadcast from '../page/broadcast'
import Group from '../page/group'
import Main from '../page/main'
import Film from '../components/audio/Film'
import Read from '../components/audio/Read'
import Mv from '../components/audio/Mv'
import City from '../components/audio/City'
import Music from '../components/audio/Music'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HOME',
      component: Home
    },
    {
    	path: '/home',
    	name: 'Home',
    	component: Home
    },
    {
    	path: '/audio',
    	//name: 'Audio',
    	component: Audio,
    	children: [
    		{path: "", component: Film},
    		{path: "film", component: Film},/*   
    										 template html*/
    		{path: "read", component: Read},
    		{path: "mv", component: Mv},
    		{path: "city", component:City},
    		{path: "music", component: Music}
    	]
    },
    {
    	path: '/broadcast',
    	name: 'Broadcast',
    	component: Broadcast
    },
    {
    	path: '/group',
    	name: 'Group',
    	component: Group
    },
    {
    	path: '/main',
    	name: 'Main',
    	component: Main
    }
  ]
})



