
<template id="item">
	<div class="itemWrap" @click="fn">
		<span v-show="bool"><slot name="normalImg"></slot></span>
		<span v-show="!bool"><slot name="activeImg"></slot></span>
		<h5 :class="{active: !bool}">{{ txt }}</h5> 
	</div>
</template>

<script type="text/javascript">
	export default{
		props: ["txt","mark","sel"],
		methods: {
			fn: function(){
				this.$emit("change",this.mark);
				this.$router.push("/"+this.mark);
			}
		},
		computed: {
			bool: function(){
				if(this.mark == this.sel){
					return false;
				}else{
					return true;
				}
			}
		}
	}
</script>

<style type="text/css">
	*{margin:0;padding:0;}
	.itemWrap{float:left;width:20%;}
	.active{color:#66d77b;}
	.itemWrap h5{font-weight:normal;color:#666;font-size:12px;color:#666;height:14px;line-height:14px;}
</style>
