<template>
	<div class="headerWrap" :class="{green: isGreen}">
		<span><slot name="one"></slot></span>
		<span><slot name="two"></slot></span>
		<span><slot name="three"></slot></span>
		<span><slot name="four"></slot></span>
		<h2>{{ con }}</h2>
	</div>	
</template>
<script type="text/javascript">
	export default{
		props: ["con", "isGreen"]
	}
</script>
<style type="text/css">
	.headerWrap{height:44px;border-bottom:1px solid #bbb;line-height:44px;}
	.headerWrap h2{font-size:18px;color:#666;}
	.green{background-color:#42c055;}
</style>


